"use client";

import { Editor, useMonaco } from "@monaco-editor/react";
import { type editor, Uri } from "monaco-editor";
import { useEffect, useRef, useState } from "react";

export function MonacoEditor() {
  const [model, setModel] = useState<string>(
    `console.log('Hello World')

console.log('Hello World')

console.log('Hello World')
`,
  );
  const [editorValue, setEditorValue] = useState<string>(model);
  const editorModel = useRef<editor.ITextModel | null>(null);

  const monaco = useMonaco();

  useEffect(() => {
    if (monaco?.editor) {
      editorModel.current = monaco.editor.getModel(Uri.file("default.ts"));
    }
  }, [monaco?.editor]);

  useEffect(() => {
    if (editorModel.current && editorValue !== model) {
      editorModel.current.setValue(editorValue);
    }
  }, [editorValue, model]);

  return (
    <Editor
      className="h-full w-full grow"
      path="default.ts"
      wrapperProps={{
        className: "w-full grow flex flex-col",
      }}
      language="typescript"
      defaultValue={model}
      value={model}
      theme="vs-dark"
      options={{
        minimap: {
          enabled: false,
        },
        value: model,
      }}
      onChange={(value, event) => {
        console.debug(event);

        const allChangesValid = event.changes.every(
          (change) =>
            change.range.startLineNumber > 2 && change.range.endLineNumber < 4,
        );

        console.debug(allChangesValid);
        setEditorValue(model);

        if (allChangesValid) {
          setModel(value ?? "");
        }
      }}
    />
  );
}
